<template>
	<div class="score-22" v-loading="mainLoading" element-loading-spinner="iconfont icon-loading1" element-loading-background="rgba(0, 0, 0, 0.2)">
		<!-- <area-panel :title-left="true" :area-title="`各区直报评分<i>(${statisticsMonth})</i>`">
			<div slot="subTitle" class="head-sub-title">明细>></div> -->
		<area-panel area-title="各区直报评分">
			<div slot="subTitle" class="head-sub-title">
				<span>{{ statisticsMonth }}</span>
			</div>
			<div slot="panelMain" class="module-body">
				<div class="legend" v-show="hasData">
					<div class="legend-item">
						<span></span>
						<span>保障体系</span>
					</div>
					<div class="legend-item">
						<span></span>
						<span>直报覆盖率</span>
					</div>
					<div class="legend-item">
						<span></span>
						<span>直报质量</span>
					</div>
					<div class="legend-item">
						<span></span>
						<span>争先创优</span>
					</div>
				</div>
				<div class="echart-container" ref="EContainer" v-show="hasData"></div>
				<div class="no-data" v-show="!hasData"><span>暂无数据</span></div>
			</div>
		</area-panel>
	</div>
</template>

<script>
import areaPanel from '@/components/overview/area-panel';
import {areaLevelCorAreaId, maxDecimalPlaces} from '@/common/fn';
import {getStorage} from '@/utils/storage';

import {areaData} from '@/common/config/filterArea.js';

const subVo = getStorage('govGovernmentSubjectVO');
// echarts 超级变量含义 {a} series.name, {b} data.name {c} data.value {d} data%
const barOption = {
	grid: {
		show: false,
		// height: '80%',
		top: 20,
		left: '5%',
		right: '4%',
		bottom: 32,
	},
	tooltip: {
		show: true,
		trigger: 'item',
		confine: true,
		formatter(a) {
			// console.log(a);
			return `<span style="color: #9DD1D7;font-weight: bold; display: block;">${a.name}</span>
			<span style="color: #6DC1CB; display: block;">保障体系：${a.data.securitySystemScore}分</span>
			<span style="color: #6DC1CB; display: block;">直报覆盖率：${a.data.coverageScore}分</span>
			<span style="color: #6DC1CB; display: block;">直报质量：${a.data.massScore}分</span>
			<span style="color: #6DC1CB; display: block;">争先创优：${a.data.striveForExcellenceScore}分</span>`;
		},
		backgroundColor: '#021F33',
		padding: [13, 12, 13, 12],
	},
	xAxis: {
		data: [],
		axisLabel: {
			interval: 0,
			textStyle: {
				color: '#6EC2CB',
				fontSize: 12,
				lineHeight: 14,
			},
			align: 'center',
		},
		axisTick: {
			show: false,
		},
		axisLine: {
			show: true,
			lineStyle: {
				color: 'rgba(109, 193, 203, 0.2)',
			},
		},
	},
	yAxis: {
		show: true,
		axisLine: {
			show: false,
		},
		axisTick: {
			show: false,
		},
		splitLine: {
			show: true,
			lineStyle: {
				color: 'rgba(109, 193, 203, 0.2)',
			},
		},
		axisLabel: {
			show: false,
		},
	},
	series: [
		// 每个堆叠顺序是从下往上
		{
			type: 'bar',
			barWidth: '40%',
			data: [],
			stack: '总量',
			itemStyle: {
				color: {
					type: 'linear',
					x: 0,
					y: 0,
					x2: 0,
					y2: 1,
					colorStops: [
						{
							offset: 0,
							color: '#32CEBB', // 0% 处的颜色
						},
						{
							offset: 1,
							color: '#32CEBB', // 100% 处的颜色
						},
					],
					global: false, // 缺省为 false
				},
			},
		},
		{
			type: 'bar',
			barWidth: '40%',
			data: [],
			stack: '总量',
			itemStyle: {
				color: {
					type: 'linear',
					x: 0,
					y: 0,
					x2: 0,
					y2: 1,
					colorStops: [
						{
							offset: 0,
							color: '#F6A82B', // 0% 处的颜色
						},
						{
							offset: 1,
							color: '#F6A82B', // 100% 处的颜色
						},
					],
					global: false, // 缺省为 false
				},
			},
		},
		{
			type: 'bar',
			barWidth: '40%',
			data: [],
			stack: '总量',
			itemStyle: {
				color: {
					type: 'linear',
					x: 0,
					y: 0,
					x2: 0,
					y2: 1,
					colorStops: [
						{
							offset: 0,
							color: '#10AC2A', // 0% 处的颜色
						},
						{
							offset: 1,
							color: '#10AC2A', // 100% 处的颜色
						},
					],
					global: false, // 缺省为 false
				},
			},
		},
		{
			type: 'bar',
			barWidth: '40%',
			data: [],
			stack: '总量',
			itemStyle: {
				color: {
					type: 'linear',
					x: 0,
					y: 0,
					x2: 0,
					y2: 1,
					colorStops: [
						{
							offset: 0,
							color: '#3649FB', // 0% 处的颜色
						},
						{
							offset: 1,
							color: '#3649FB', // 100% 处的颜色
						},
					],
					global: false, // 缺省为 false
				},
			},
		},
		{
			// 背景bar
			type: 'bar',
			// silent: true,
			barWidth: '40%',
			barGap: '-100%', // Make series be ove
			data: [],
			itemStyle: {
				color: 'transparent',
			},
			label: {
				show: true,
				position: 'top',
				color: '#FBE948',
				fontSize: 12,
				fontWeight: 'bold',
				formatter({data}) {
					// console.log('data=======>', data);
					return `${data.value}分`;
				},
			},
		},
	],
};
export default {
	name: 'score-22',
	data() {
		return {
			mainLoading: true,
			chartInstance: null,
			hasData: true,
			statisticsMonth: '',
		};
	},
	mounted() {
		this.$nextTick(() => {
			this.initMoudle();
		});
		// 适配图表label字体大小
		window.addEventListener('resize', this.adaptationChart);
		this.$once('hook:beforeDestroy', function () {
			window.removeEventListener('resize', this.adaptationChart);
		});
	},
	methods: {
		async initMoudle() {
			this.hasData = this.mainLoading = true;
			this.chartInstance = this._initEcharts(this.eContainerRef, barOption);
			this.refreshChart(this.chartInstance);
			this.adaptationChart();
		},
		refreshRender() {
			if (this.chartInstance) {
				this.chartInstance.resize();
			}
		},
		_initEcharts(el, option) {
			const chart = this.$echarts.init(el);
			chart.setOption(option);
			return chart;
		},
		async refreshChart(chartInstance) {
			const res = await this.getListScoreByAreaIds();

			// console.log('res========>', res)
			if (!res || !res.length) {
				this.mainLoading = false;
				this.hasData = false;
				return;
			}
			const tempMonth = res[0].statisticsMonth.split('-');
			this.statisticsMonth = tempMonth[0] + '年' + tempMonth[1] + '月';

			let tempData = [];

			// 保持获取的数据中与areaData中的数据顺序一致性
			areaData.forEach(ele => {
				let empty = res.filter(item => item.areaId === ele.id);
				if (!empty.length) {
					res.push({
						areaId: ele.id,
						coverageScore: 0,
						massScore: 0,
						securitySystemScore: 0,
						striveForExcellenceScore: 0,
					});
				}
				res.forEach(item => {
					if (item.areaId === ele.id) {
						tempData.push(item);
					}
				});
			});

			let seriesData = [
				{
					title: '保障体系',
					data: tempData.map(item =>
						maxDecimalPlaces({
							value: item.securitySystemScore ? item.securitySystemScore : 0,
							places: 0,
						})
					),
				},
				{
					title: '直报覆盖率',
					data: tempData.map(item =>
						maxDecimalPlaces({
							value: item.coverageScore ? item.coverageScore : 0,
							places: 0,
						})
					),
				},
				{
					title: '直报质量',
					data: tempData.map(item =>
						maxDecimalPlaces({
							value: item.massScore ? item.massScore : 0,
							places: 0,
						})
					),
				},
				{
					title: '争先创优',
					data: tempData.map(item =>
						maxDecimalPlaces({
							value: item.striveForExcellenceScore ? item.striveForExcellenceScore : 0,
							places: 0,
						})
					),
				},
			];

			// console.log('seriesData========>', seriesData)

			// 图表柱子个颜色是从下往上，所以翻转数组，与图表一致
			seriesData.reverse();

			const chartOpts = {
				xAxis: {
					data: areaData.map(item => {
						return item.areaName;
					}),
				},
				series: [
					{
						data: seriesData[0].data.map(item => {
							return {
								value: item,
								title: seriesData[0].title,
							};
						}),
					},
					{
						data: seriesData[1].data.map(item => {
							return {
								value: item,
								title: seriesData[1].title,
							};
						}),
					},
					{
						data: seriesData[2].data.map(item => {
							return {
								value: item,
								title: seriesData[2].title,
							};
						}),
					},
					{
						data: seriesData[3].data.map(item => {
							return {
								value: item,
								title: seriesData[3].title,
							};
						}),
					},
					{
						data: tempData.map(item => {
							return {
								value: maxDecimalPlaces({
									value: item.securitySystemScore + item.coverageScore + item.massScore + item.striveForExcellenceScore,
									places: 0,
								}),
								securitySystemScore: maxDecimalPlaces({value: item.securitySystemScore, places: 0}),
								coverageScore: maxDecimalPlaces({value: item.coverageScore, places: 0}),
								massScore: maxDecimalPlaces({value: item.massScore, places: 0}),
								striveForExcellenceScore: maxDecimalPlaces({value: item.striveForExcellenceScore, places: 0}),
							};
						}),
					},
				],
			};
			chartInstance.setOption(chartOpts);
			this.mainLoading = false;
		},
		// handlePage() {
		// 	// this.$router.push({
		// 	// 	name: 'operateSubjectList'
		// 	// });
		// 	this.$emit('handleClick', 'operateSubjectList', '经营规模统计');
		// },
		getListScoreByAreaIds() {
			let ids = areaData.map(item => item.id);
			return this.$ajax
				.post(this.$api.produce.listScoreByAreaIds, {
					farmAreaId: ids,
					farmAreaType: areaLevelCorAreaId().farmAreaType,
				})
				.then(({data}) => {
					if (data.respCode === 0) {
						return data.obj;
					} else {
						return null;
					}
				})
				.catch(e => {
					console.log(new Error(e));
				});
		},
		adaptationChart() {
			const htmlWidth = document.documentElement.clientWidth || document.body.clientWidth;
			const opts = {
				xAxis: {
					axisLabel: {
						textStyle: {
							fontSize: 12,
						},
					},
				},
				series: [
					{},
					{},
					{},
					{},
					{
						label: {
							fontSize: 12,
						},
					},
				],
			};
			if (htmlWidth > 3000) {
				opts.xAxis.axisLabel.textStyle.fontSize = 16;
				opts.series[4].label.fontSize = 16;
				if (this.chartInstance) {
					this.chartInstance.setOption(opts);
				}
			} else {
				opts.xAxis.axisLabel.textStyle.fontSize = 12;
				opts.series[4].label.fontSize = 12;
				if (this.chartInstance) {
					this.chartInstance.setOption(opts);
				}
			}
		},
	},
	computed: {
		eContainerRef() {
			return this.$refs.EContainer;
		},
	},
	components: {
		areaPanel,
	},
};
</script>

<style scoped lang="less">
.score-22 {
	width: 100%;
	height: 100%;
}
// 头部公共样式自定义
// /deep/.area-head-bar h2.head-title span i {
// 	font-size: 12px;
// 	vertical-align: -3px;
// }
.module-body {
	display: flex;
	flex-direction: column;
	width: 100%;
	height: 100%;
	overflow: hidden;

	.legend {
		display: flex;
		justify-content: space-around;
		padding: 0 10px;

		.legend-item {
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 5px 0 10px 0;
			font-size: 12px;
			font-family: SimSun;
			font-weight: 400;
			color: #6dc1cb;

			span:first-child {
				display: inline-block;
				width: 6px;
				height: 6px;
				margin-right: 5px;
				border-radius: 50%;
			}

			&:nth-child(1) {
				span:first-child {
					background-color: #3649fb;
				}
			}
			&:nth-child(2) {
				span:first-child {
					background-color: #10ac2a;
				}
			}
			&:nth-child(3) {
				span:first-child {
					background-color: #f6a82b;
				}
			}
			&:nth-child(4) {
				span:first-child {
					background-color: #32cebb;
				}
			}
		}
	}

	.echart-container {
		flex: 1;
		height: 100%;
	}
	.no-data {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 14px;
		color: #00f6ff;
		font-weight: bold;
	}
}
/deep/ .el-loading-spinner {
	overflow: hidden;
	.iconfont {
		display: block;
		font-size: 40px;
		color: #00f6ff;
		animation: loading 1.25s linear infinite;
		margin-top: 24px;
		margin-left: 8px;
	}
}
/* 32:9超宽屏幕适配 */
@media screen and (min-width: 3000px) {
	.module-body .legend .legend-item {
		font-size: 15px;
	}
}
</style>



